<template>
    <img :src="image" class="imgCours" alt="algebre" @click="open()">
    <div class="card-content">
        <h2>
            {{ name }}
        </h2>
        <p>
            Matière: {{ matter }}<br>Auteur: {{ auteur }} <br>Année: {{ year }}<br>Nom: {{ name }}
        </p>
        <a href="#" class="button">
            Download
            <span class="material-icons">
                arrow_right_alt
            </span>
        </a>
    </div>
</template>

<script>
import algebre from '../../../assets/matter/algebre.png';
import analyse from '../../../assets/matter/analyse.png';
import analyseNum from '../../../assets/matter/analyseNum.png';
import auto from '../../../assets/matter/auto.png';
import bdr from '../../../assets/matter/bdr.png';
import calculformel from '../../../assets/matter/calculformel.png';
import cdm from '../../../assets/matter/cdm.png';
import electCin from '../../../assets/matter/electCin.png';
import electMag from '../../../assets/matter/electMag.webp';
import electStat from '../../../assets/matter/electStat.png';
import genielogiciel from '../../../assets/matter/genielogiciel.png';
import info from '../../../assets/matter/info.png';
import meca from '../../../assets/matter/meca.png';
import omp from '../../../assets/matter/omp.png';
import optiqueGeo from '../../../assets/matter/optiqueGeo.png';
import optiqueOnde from '../../../assets/matter/optiqueOnde.png';
import pdo from '../../../assets/matter/pdo.png';
import proba from '../../../assets/matter/proba.png';
import thermo from '../../../assets/matter/thermo.png';
import archi from '../../../assets/matter/archi.png';
import magneto from '../../../assets/matter/magneto.png';


export default {
    props: {
        matter: String,
        name: String,
        auteur: String,
        year: Number,
        path: String
    },
    data() {
        return {
            link: '',
            image: null
        }
    },
    methods: {
        open: async function () {
            const url = '/uploads/' + this.path + '.pdf'
            const a = document.createElement('a')
            a.href = url
            a.download = url.split('/').pop()
            document.body.appendChild(a)
            a.click()
            document.body.removeChild(a)
        },
    },
    mounted() {
        console.log(this.path);
        switch (this.matter) {
            case "Architecture":
                this.image = archi
                break;
            case "Magnetostatique":
                this.image = magneto;
                break;
            case "Analyse":
                // img[this.number - 1].src = 'img/analyse.2d19d7af.png'
                this.image = analyse;
                break;
            case "Algebre":
                // img[this.number - 1].src = 'img/algebre.png'
                this.image = algebre;

                break;
            case "Analyse numerique":
                // img[this.number - 1].src = 'img/analyseNum.712aac4e.png'
                this.image = analyseNum;

                break;
            case "Automatique":
                // img[this.number - 1].src = 'img/auto.3e7ccc85.png'
                this.image = auto;

                break;
            case "Base De Donnees":
                // img[this.number - 1].src = 'img/bdr.0b1fd181.png'
                this.image = bdr;

                break;
            case "Calcul formel":
                // img[this.number - 1].src = 'img/calculformel.3979adb4.png'
                this.image = calculformel;

                break;
            case "Chime des Materiaux":
                // img[this.number - 1].src = 'img/cdm.09694a3d.png'
                this.image = cdm;

                break;
            case "Electrocinetique":
                // img[this.number - 1].src = 'img/electCin.ccaa9928.png'
                this.image = electCin;

                break;
            case "Electromagnetisme":
                // img[this.number - 1].src = 'img/electMag.6dd482eb.webp'
                this.image = electMag;

                break;
            case "Electrostatique":
                // img[this.number - 1].src = 'img/electStat.f5776e7f.png'
                this.image = electStat;

                break;
            case "Genie logiciel":
                // img[this.number - 1].src = 'img/genielogiciel.00c7def2.png'
                this.image = genielogiciel;

                break;
            case "Algo":
                // img[this.number - 1].src = 'img/info.313aba42.png'
                this.image = info;

                break;
            case "Mecanique":
                // img[this.number - 1].src = 'img/meca.a410631c.png'
                this.image = meca;

                break;
            case "OMP":
                // img[this.number - 1].src = 'img/omp.4c9fc311.png'
                this.image = omp;

                break;
            case "Optique geometrique":
                // img[this.number - 1].src = 'img/optiqueGeo.8ee32d35.png'
                this.image = optiqueGeo;

                break;
            case "Optique ondulatoire":
                // img[this.number - 1].src = 'img/optiqueOnde.7f48be91.png'
                this.image = optiqueOnde;

                break;
            case "Physique des ondes":
                // img[this.number - 1].src = 'img/pdo.1dcecde4.png'
                this.image = pdo;

                break;
            case "Probabilite":
                // img[this.number - 1].src = 'img/proba.a8ed476f.png'
                this.image = proba;

                break;
            case "Thermodynamique":
                // img[this.number - 1].src = 'img/thermo.7a322dae.png'
                this.image = thermo;

                break;

            default:
                break;
        }
    }
}
</script>

<style lang="scss">
@media screen {
    img {
        position: absolute;
        object-fit: cover;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        opacity: 0.9;
        transition: opacity .2s ease-out;
    }

    .card-content {
        h2 {
            position: absolute;
            inset: auto auto 30px 30px;
            margin: 0;
            transition: inset .3s .3s ease-out;
            font-family: 'Roboto Condensed', sans-serif;
            font-weight: normal;
            text-transform: uppercase;
            font-size: 25px;
        }

        p,
        a {
            position: absolute;
            opacity: 0;
            max-width: 80%;
            transition: opacity .3s ease-out;
            font-size: 15px;
        }

        p {
            line-height: 30px;
            inset: auto auto 50px 30px;
        }

        a {
            inset: auto auto 20px 150px;
            color: inherit;
            text-decoration: none;

            .material-icons {
                vertical-align: middle;
            }

            &:hover {
                text-decoration: underline;
            }
        }


    }
}

@media screen and (max-width: 600px) {
    img {
        position: absolute;
        object-fit: cover;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        opacity: 1;
    }

    .card-content {
        h2 {
            position: absolute;
            inset: auto auto 190px 30px;
            margin: 0;
            font-family: 'Roboto Condensed', sans-serif;
            font-weight: 800;
            text-transform: uppercase;
            font-size: 15px;
            color: red;
        }

        p,
        a {
            position: absolute;
            text-shadow: 2px 0 #fff, -2px 0 #fff, 0 2px #fff, 0 -2px #fff,
                1px 1px #fff, -1px -1px #fff, 1px -1px #fff, -1px 1px #fff;
            opacity: 1;
            color: red;
            max-width: 80%;
            font-size: 10px;
        }

        p {
            line-height: 20px;
            inset: auto auto 50px 30px;
        }

        a {
            inset: auto auto 20px 150px;
            color: inherit;
            text-decoration: none;

            .material-icons {
                vertical-align: middle;
            }

            text-decoration: underline;
        }


    }
}
</style>