<template>
    <div class="cours-page-container">
      <div class="container-cours">
        <div class="left-container-cours">
          <div class="title-cours">
            <span class="title">
              {{ title }}
            </span>
          </div>
          <div class="cours-selector-container">
            <div class="cours-selector" id="cm">
              <p>Cours</p>
              <span class="material-icons">
                chevron_right
              </span>
            </div>
            <div class="cours-selector" id="td">
              <p>Travaux Dirigés</p>
              <span class="material-icons">
                chevron_right
              </span>
            </div>
            <div class="cours-selector" id="fiche">
              <p>Fiches</p>
              <span class="material-icons">
                chevron_right
              </span>
            </div>
            <div class="cours-selector" id="partiel">
              <p>Partiels</p>
              <span class="material-icons">
                chevron_right
              </span>
            </div>
            <div class="cours-selector" id="tp">
              <p>Travaux Pratiques</p>
              <span class="material-icons">
                chevron_right
              </span>
            </div>
          </div>
  
        </div>
        <div class="right-container-cours">
          <div class="cours-result-years">
            <span class="title">
              SHPI 1A :
            </span>
            <span class="material-icons">
              chevron_right
            </span>
          </div>
          <div v-if="isReleve1">
            <CardCoursContainerVue v-for="(index) in Object.keys(arrayDisplay1).length" :key="index"
              :data="arrayDisplay1[index - 1]" :number="number1" :index="index"></CardCoursContainerVue>
          </div>
          <div class="cours-result-years">
            <span class="title">
              SHPI 2A :
            </span>
            <span class="material-icons">
              chevron_right
            </span>
          </div>
          <div v-if="isReleve2">
            <CardCoursContainerVue v-for="(index) in Object.keys(arrayDisplay2).length" :key="index"
              :data="arrayDisplay2[index - 1]" :number="number2" :index="index"></CardCoursContainerVue>
          </div>
          <div class="cours-result-years">
            <span class="title">
              SHPI 3A :
            </span>
            <span class="material-icons">
              chevron_right
            </span>
          </div>
        </div>
      </div>
    </div>
  </template>
  
  <script>
  import axios from 'axios';
  import CardCoursContainerVue from './component/CardCoursContainer.vue';
  export default {
    components: {
      CardCoursContainerVue,
    },
    data() {
      const research = this.$route.query.research;
      const year = this.$route.query.year;
      const matter = this.$route.query.matter;
      let title = '';
  
      if(matter != 0){ title = matter }
      else if (research != 0){ title = research }
      else { title = 'Cours'}
  
      return {
        title,
        isCm: true,
        isTd: false,
        isFiche: false,
        isPartiel: false,
        isTp: false,
        isReleve1: false,
        isReleve2: false,
        isReleve3: false,
        research,
        year,
        matter,
        arrayDisplay1: [],
        arrayDisplay2: [],
        cm1: {},
        td1: {},
        fiche1: {},
        partiel1: {},
        tp1: {},
        cm2: {},
        td2: {},
        fiche2: {},
        partiel2: {},
        tp2: {},
        number1: 0,
        number2: 0,
        tdComp1: 0,
        tpComp1: 0,
        ficheComp1: 0,
        cmComp1: 0,
        partielComp1: 0,
        tdComp2: 0,
        tpComp2: 0,
        ficheComp2: 0,
        cmComp2: 0,
        partielComp2: 0,
      }
    },
    methods: {
      allRecords: async function () {
        try {
          let cours = [];
          const response = await axios.get("/api/cours");
          let regex = new RegExp(this.research, 'i');
                let test = response.data.filter(cour => regex.test(cour.name));
          let temp_array = test;
          for (let i = 0; i < temp_array.length; i++) {
            if (!(this.year == 0) && !(this.matter.length == 0)) {
              if (temp_array[i].shpi == this.year && temp_array[i].matiere == this.matter) {
                cours.push(temp_array[i])
              }
            }
            else if (this.matter.length == 0 && this.year.length == 0) {
              cours.push(temp_array[i])
            }
            else if (this.matter.length == 0) {
              if (temp_array[i].shpi == this.year) {
                cours.push(temp_array[i])
              }
            }
            else if (this.year == 0) {
              if (temp_array[i].matiere == this.matter) {
                cours.push(temp_array[i])
              }
            }
          }
          let tdComp1 = 0;
          let tpComp1 = 0;
          let ficheComp1 = 0;
          let cmComp1 = 0;
          let partielComp1 = 0;
          let tdComp2 = 0;
          let tpComp2 = 0;
          let ficheComp2 = 0;
          let cmComp2 = 0;
          let partielComp2 = 0;
          let tdObj1 = JSON.parse(JSON.stringify(this.td1));
          let tpObj1 = JSON.parse(JSON.stringify(this.tp1));
          let cmObj1 = JSON.parse(JSON.stringify(this.cm1));
          let ficheObj1 = JSON.parse(JSON.stringify(this.fiche1));
          let partielObj1 = JSON.parse(JSON.stringify(this.partiel1));
          let tdObj2 = JSON.parse(JSON.stringify(this.td2));
          let tpObj2 = JSON.parse(JSON.stringify(this.tp2));
          let cmObj2 = JSON.parse(JSON.stringify(this.cm2));
          let ficheObj2 = JSON.parse(JSON.stringify(this.fiche2));
          let partielObj2 = JSON.parse(JSON.stringify(this.partiel2));
  
          let temp_array1 = [];
          let temp_array2 = [];
  
          for (let i = 0; i < cours.length; i++) {
            if (cours[i].shpi == "1") {
              temp_array1.push(cours[i]);
            }
          }
  
          for (let i = 0; i < cours.length; i++) {
            if (cours[i].shpi == "2") {
              temp_array2.push(cours[i]);
            }
          }
  
          for (let i = 0; i < temp_array1.length; i++) {
            switch (temp_array1[i].type) {
              case "Td":
                if (tdComp1 % 3 == 0) {
                  tdObj1[Math.floor(tdComp1 / 3).toString()] = [temp_array1[i]]
                }
                else {
                  tdObj1[Math.floor(tdComp1 / 3)].push(temp_array1[i]);
                }
                tdComp1 = tdComp1 + 1;
                break;
              case "Cm":
                if (cmComp1 % 3 == 0) {
                  cmObj1[Math.floor(cmComp1 / 3).toString()] = [temp_array1[i]]
                }
                else {
                  cmObj1[Math.floor(cmComp1 / 3)].push(temp_array1[i]);
                }
                cmComp1 = cmComp1 + 1;
                break;
              case "Fiches":
                if (ficheComp1 % 3 == 0) {
                  ficheObj1[Math.floor(ficheComp1 / 3).toString()] = [temp_array1[i]]
                }
                else {
                  ficheObj1[Math.floor(ficheComp1 / 3)].push(temp_array1[i]);
                }
                ficheComp1 = ficheComp1 + 1;
                break;
              case "Tp":
                if (tpComp1 % 3 == 0) {
                  tpObj1[Math.floor(tpComp1 / 3).toString()] = [temp_array1[i]]
                }
                else {
                  tpObj1[Math.floor(tpComp1 / 3)].push(temp_array1[i]);
                }
                tpComp1 = tpComp1 + 1;
                break;
              case "Partiels":
                if (partielComp1 % 3 == 0) {
                  partielObj1[Math.floor(partielComp1 / 3).toString()] = [temp_array1[i]]
                }
                else {
                  partielObj1[Math.floor(partielComp1 / 3)].push(temp_array1[i]);
                }
                partielComp1 = partielComp1 + 1;
                break;
  
              default:
                break;
            }
          }
          for (let i = 0; i < temp_array2.length; i++) {
            switch (temp_array2[i].type) {
              case "Td":
                if (tdComp2 % 3 == 0) {
                  tdObj2[Math.floor(tdComp2 / 3).toString()] = [temp_array2[i]]
                }
                else {
                  tdObj2[Math.floor(tdComp2 / 3)].push(temp_array2[i]);
                }
                tdComp2 = tdComp2 + 1;
                break;
              case "Cm":
                if (cmComp2 % 3 == 0) {
                  cmObj2[Math.floor(cmComp2 / 3).toString()] = [temp_array2[i]]
                }
                else {
                  cmObj2[Math.floor(cmComp2 / 3)].push(temp_array2[i]);
                }
                cmComp2 = cmComp2 + 1;
                break;
              case "Fiches":
                if (ficheComp2 % 3 == 0) {
                  ficheObj2[Math.floor(ficheComp2 / 3).toString()] = [temp_array2[i]]
                }
                else {
                  ficheObj2[Math.floor(ficheComp2 / 3)].push(temp_array2[i]);
                }
                ficheComp2 = ficheComp2 + 1;
                break;
              case "Tp":
                if (tpComp2 % 3 == 0) {
                  tpObj2[Math.floor(tpComp2 / 3).toString()] = [temp_array2[i]]
                }
                else {
                  tpObj2[Math.floor(tpComp2 / 3)].push(temp_array2[i]);
                }
                tpComp2 = tpComp2 + 1;
                break;
              case "Partiels":
                if (partielComp2 % 3 == 0) {
                  partielObj2[Math.floor(partielComp2 / 3).toString()] = [temp_array2[i]]
                }
                else {
                  partielObj2[Math.floor(partielComp2 / 3)].push(temp_array2[i]);
                }
                partielComp2 = partielComp2 + 1;
                break;
  
              default:
                break;
            }
          }
          this.td1 = tdObj1;
          this.cm1 = cmObj1;
          this.tp1 = tpObj1;
          this.fiche1 = ficheObj1;
          this.partiel1 = partielObj1;
  
          this.tdComp1 = tdComp1;
          this.tpComp1 = tpComp1;
          this.ficheComp1 = ficheComp1;
          this.cmComp1 = cmComp1;
          this.partielComp1 = partielComp1;
  
          this.td2 = tdObj2;
          this.cm2 = cmObj2;
          this.tp2 = tpObj2;
          this.fiche2 = ficheObj2;
          this.partiel2 = partielObj2;
  
          this.tdComp2 = tdComp2;
          this.tpComp2 = tpComp2;
          this.ficheComp2 = ficheComp2;
          this.cmComp2 = cmComp2;
          this.partielComp2 = partielComp2;
  
          this.arrayDisplay1 = this.cm1;
          this.number1 = this.cmComp1;
          this.arrayDisplay2 = this.cm2;
          this.number2 = this.cmComp2;
  
        } catch (err) {
          console.log(err);
        }
      },
    },
    mounted() {
      this.allRecords();
  
      const coursSelected = document.querySelectorAll('.cours-selector');
      coursSelected[0].classList.add('active')
      for (let i = 0; i < 5; i++) {
        coursSelected[i].addEventListener('click', () => {
          this.isReleve1 = this.isReleve2 = this.isReleve3 = false;
          if (coursSelected[i].classList[1]) {
            coursSelected[i].classList.remove('active')
          } else {
            coursSelected[i].classList.add('active')
            switch (coursSelected[i].id) {
              case "cm":
                this.isCm = true;
                this.isFiche = this.isPartiel = this.isTd = this.isTp = false;
                break;
              case "td":
                this.isTd = true;
                this.isFiche = this.isPartiel = this.isCm = this.isTp = false;
                break;
              case "fiche":
                this.isFiche = true;
                this.isCm = this.isPartiel = this.isTd = this.isTp = false;
                break;
              case "partiel":
                this.isPartiel = true;
                this.isFiche = this.isCm = this.isTd = this.isTp = false;
                break;
              case "tp":
                this.isTp = true;
                this.isFiche = this.isPartiel = this.isTd = this.isCm = false;
                break;
  
              default:
              break;
            }
  
  
            for (let j = 0; j < 5; j++) {
              if (i != j) { coursSelected[j].classList.remove('active') }
            }
          }
        })
      }
  
      const coursResultYear = document.querySelectorAll('.cours-result-years');
  
      for (let i = 0; i < 3; i++) {
        coursResultYear[i].addEventListener('click', () => {
          switch (i) {
            case 0:
              this.isReleve1 = !this.isReleve1;
              if (this.isReleve1) {
                this.isReleve2 = this.isReleve3 = false;
                coursResultYear[i].children[1].innerText = "expand_more"
                if (this.isCm) {
                  this.arrayDisplay1 = this.cm1;
                  this.number1 = this.cmComp1;
                }
                else if (this.isTd) {
                  this.arrayDisplay1 = this.td1;
                  this.number1 = this.tdComp1;
                }
                else if (this.isFiche) {
                  this.arrayDisplay1 = this.fiche1;
                  this.number1 = this.ficheComp1;
                }
                else if (this.isPartiel) {
                  this.arrayDisplay1 = this.partiel1;
                  this.number1 = this.partielComp1;
                }
                else if (this.isTp) {
                  this.arrayDisplay1 = this.tp1;
                  this.number1 = this.tpComp1;
                }
              }
              else {
                coursResultYear[i].children[1].innerText = "chevron_right"
              }
              break;
            case 1:
              this.isReleve2 = !this.isReleve2;
              this.isReleve1 = this.isReleve3 = false;
              if (this.isReleve2) {
                coursResultYear[i].children[1].innerText = "expand_more"
                if (this.isCm) {
                  this.arrayDisplay2 = this.cm2;
                  this.number2 = this.cmComp2;
                }
                else if (this.isTd) {
                  this.arrayDisplay2 = this.td2;
                  this.number2 = this.tdComp2;
                }
                else if (this.isFiche) {
                  this.arrayDisplay2 = this.fiche2;
                  this.number2 = this.ficheComp2;
                }
                else if (this.isPartiel) {
                  this.arrayDisplay2 = this.partiel2;
                  this.number2 = this.partielComp2;
                }
                else if (this.isTp) {
                  this.arrayDisplay2 = this.tp2;
                  this.number2 = this.tpComp2;
                }
              } else { 
                coursResultYear[i].children[1].innerText = "chevron_right"
              }
              break;
            case 2:
              this.isReleve3 = !this.isReleve3;
              this.isReleve2 = this.isReleve1 = false;
              if (this.isReleve3) { coursResultYear[i].children[1].innerText = "expand_more" } else { coursResultYear[i].children[1].innerText = "chevron_right" }
              break;
            default:
              break;
          }
        })
      }
    },
  
    // watch: {
    //   isCm: function (bool) {
    //     if (bool) {
    //       this.arrayDisplay = this.cm;
    //       this.number = this.cmComp;
    //     }
    //   },
    //   isTp: function (bool) {
    //     if (bool) {
  
  
    //     }
    //   },
    //   isTd: function (bool) {
    //     if (bool) {
  
    //     }
    //   },
    //   isFiche: function (bool) {
    //     if (bool) {
  
    //     }
    //   },
    //   isPartiel: function (bool) {
    //     if (bool) {
  
    //     }
    //   },
    // }
  
  }
  </script>
  
  <style lang="scss">
  .cours-page-container {
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  
    .container-cours {
      width: 90%;
      height: 90%;
      box-shadow: rgba(255, 255, 255, 0.35) 0px 5px 15px;
      border-radius: 15px;
      display: flex;
    }
  
    .left-container-cours {
      width: 25%;
      height: 100%;
      border-radius: 15px 0 0 15px;
      border-right: 2px solid white;
  
      // background-color: #fff;
      .title-cours {
        width: 100%;
        height: 5%;
        margin-top: 5%;
        display: flex;
        justify-content: center;
        align-items: center;
  
        .title {
          color: white;
          font-size: 25px;
          font-weight: 700;
          letter-spacing: 2px;
        }
      }
  
      .cours-selector-container {
        width: 100%;
        height: 90%;
        display: flex;
        align-items: flex-end;
        flex-direction: column;
        margin-top: 10%;
  
  
        .cours-selector {
          color: white;
          display: flex;
          align-items: center;
          justify-content: flex-end;
          width: 60%;
          height: 5%;
          margin-top: 10px;
          font-size: 15px;
          border-radius: 15px 0 0 15px;
          border: 2px solid white;
          border-right: none;
          cursor: pointer;
          transition: 0.3s;
  
          &:hover {
            color: red;
            border-color: red;
            transform: scale(1.2);
            border-right: 2px solid red;
            border-radius: 15px;
          }
  
          &.active {
            color: red;
            border-color: red;
            transform: scale(1.2);
            border-right: 2px solid red;
            border-radius: 15px;
          }
        }
      }
    }
  
    .right-container-cours {
      width: 75%;
      height: 100%;
      border-radius: 0 15px 15px 0;
      border-left: none;
      overflow-y: scroll;
  
      .cours-result-years {
        width: 100%;
        height: 10%;
        display: flex;
        justify-content: space-between;
        align-items: center;
        background: linear-gradient(to right, white 25%, #141414 50%);
        background-size: 210% 100%;
        background-position: right bottom;
        transition: all .4s ease-out;
        border-bottom: 2px solid white;
  
        .title {
          margin-left: 50px;
          color: white;
          font-size: 25px;
          font-weight: 700;
          letter-spacing: 2px;
          transition: all .5s ease-out;
  
        }
  
        .material-icons {
          color: white;
          margin-right: 50px;
          cursor: pointer;
        }
  
        &:hover {
          background-position: left bottom;
  
          .title {
            color: red;
          }
        }
      }
    }
  }
  </style>