<template>
    <div class="cours-result-matter-container">
        <div class="card-cours-container" v-for="(index) in data.length" :key="index">
            <CoursCard v-bind:path="data[index -1].path_folder" v-bind:matter="data[index -1].matiere" v-bind:year="data[index -1].year" v-bind:name="data[index -1].name" v-bind:auteur="data[index -1].auteur" v-bind:number="index-1+max"></CoursCard>
        </div>
    </div>
</template>

<script>
import CoursCard from '../card/CoursCard.vue';
export default {
    data() {
        let max1 = 0;
        let test = (this.number + 1 + (this.index - 1)*3) - this.number
        if (this.number - test < 0){
            max1 = this.number -1
        } else {
            max1 = test;
        }
        return {
            matter: 'test',
            max: max1
        }
    },
    components: {
        CoursCard,
    },
    props: {
        data: Array,
        number: Number,
        index: Number
    },
    mounted(){

        let test = (this.number + 1 + (this.index - 1)*3) - this.number
        if (this.number - test < 0){
            this.max = this.number -1
        } else this.max = test;
        // console.log(this.max);
    },
    methods: {
        test: function(i) {
            console.log(i);
            // console.log(this.data[i-1].matiere);
        }
    },
}
</script>

<style lang="scss">
.cours-result-matter-container {
    margin-top: 5%;
    width: 100%;
    height: auto;
    display: flex;
    border-bottom: 2px solid white;

    .card-cours-container {
        height: 400px;
        width: calc(80% / 3);
        margin-bottom: 5%;
        margin-left: 5%;
        background-color: #fff;
        border-radius: 10px;
        overflow: hidden;
        cursor: pointer;
        position: relative;
        color: white;
        box-shadow: 0 10px 30px 5px rgba(0, 0, 0, 0.2);

        &:hover h2 {
            inset: auto auto 190px 30px;
            transition: inset .3s ease-out;
            font-weight: 800;
            color: red;
        }

        &:hover p,
        &:hover a {
            text-shadow: 2px 0 #fff, -2px 0 #fff, 0 2px #fff, 0 -2px #fff,
               1px 1px #fff, -1px -1px #fff, 1px -1px #fff, -1px 1px #fff;
            color: red;
            opacity: 1;
            transition: opacity .5s .1s ease-in;
        }

        &:hover img {
            transition: opacity .3s ease-in;
            opacity: 1;
        }
    }
}
</style>