<template>
    <div class="cours-page-container-mobile">
        <div class="top-container">
            <div class="title-container">
                <span class="title-mobile">
                    {{ title }}
                </span>
            </div>
            <div class="filter-container">
                <select class="filter-year" name="" id="filter-year" v-model="vYear">
                    <option value="1">SHPI 1A</option>
                    <option value="2">SHPI 2A</option>
                    <option value="3">3A</option>
                </select>
                <select name="" id="filter-type" class="filter-type" v-model="vType">
                    <option value="Cm">Cours</option>
                    <option value="Td">Travaux Dirigés</option>
                    <option value="Fiches">Fiches</option>
                    <option value="Partiels">Partiels</option>
                    <option value="Tp">Travaux pratique</option>
                </select>
            </div>
        </div>
        <div class="container-cours-mobile">
            <div v-for="(index) in currentCours.length % 2 == 0 ? Math.floor(currentCours.length / 2) : Math.floor(currentCours.length / 2) + 1"
                :key="index" class="line-cours-mobile">
                <div class="for-contain" v-for="(index2) in 2" :key="index2">
                    <div v-if="index2 - 1 + index * 2 - 2 <= currentCours.length - 1" class="container-mobile-card">
                        <CoursCard v-bind:name="currentCours[index2 - 1 + index * 2 - 2].name"
                            v-bind:year="currentCours[index2 - 1 + index * 2 - 2].year"
                            v-bind:auteur="currentCours[index2 - 1 + index * 2 - 2].auteur"
                            v-bind:matter="currentCours[index2 - 1 + index * 2 - 2].matiere"
                            v-bind:path="currentCours[index2 - 1 + index * 2 - 2].path_folder">
                        </CoursCard>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import axios from 'axios';
import CoursCard from './card/CoursCard.vue';
export default {
    components: {
        CoursCard,
    },
    data() {
        return {
            vYear: '1',
            vType: 'Cm',
            title: '',
            research: '',
            matter: '',
            year: '',
            cours: [],
            currentCours: []
        }
    },
    methods: {

        setupData: function () {
            const research = this.$route.query.research;
            const year = this.$route.query.year;
            const matter = this.$route.query.matter;
            if (matter != 0) { this.title = this.matter = matter }
            else if (research != 0) { this.title = research }
            else { this.title = 'Cours' }
            if (year != 0) this.year = year;
            if (research != 0) this.research = research;
            if (year == 2) this.vYear = 2;
        },
        allRecords: async function () {
            try {
                const response = await axios.get('/api/cours');
                let regex = new RegExp(this.research, 'i');
                let test = response.data.filter(cour => regex.test(cour.name));
                response.data = test
                for (let i = 0; i < response.data.length; i++) {
                    if (!(this.year == 0) && !(this.matter.length == 0)) {
                        if (response.data[i].shpi == this.year && response.data[i].matiere == this.matter) {
                            this.cours.push(response.data[i])
                        }
                    }
                    else if (this.matter.length == 0 && this.year.length == 0) {
                        this.cours.push(response.data[i])
                    }
                    else if (this.matter.length == 0) {
                        if (response.data[i].shpi == this.year) {
                            this.cours.push(response.data[i])
                        }
                    }
                    else if (this.year == 0) {
                        if (response.data[i].matiere == this.matter) {
                            this.cours.push(response.data[i])
                        }
                    }
                }
                // console.log(this.cours.length % 2 == 0 ? Math.floor(this.cours.length / 2) : Math.floor(this.cours.length / 2) + 1)
                this.setCurrentCours();
            } catch (err) {
                console.log(err);
            }

        },
        setCurrentCours: function () {
            let cours = JSON.parse(JSON.stringify(this.cours))
            this.currentCours = [];
            for (let i = 0; i < cours.length; i++) {
                if (cours[i].type == this.vType && cours[i].shpi == (this.vYear).toString()) {
                    this.currentCours.push(cours[i]);
                }
            }
        }
    },
    watch: {
        vYear: function () {
            this.setCurrentCours()
        },
        vType: function () {
            this.setCurrentCours()
        }
    },
    mounted() {
        this.setupData();
        this.allRecords();
    }

}
</script>

<style lang="scss">
.cours-page-container-mobile {
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;

    .top-container {
        width: 100%;
        height: 20%;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        border-bottom: solid 1px white;

        .title-container {
            width: 100%;
            display: flex;
            justify-content: center;
            align-items: center;
            height: 50%;

            .title-mobile {
                color: red;
                font-size: 25px;
                font-weight: 700;
                letter-spacing: 2px;
            }
        }

        .filter-container {
            width: 100%;
            height: 50%;
            display: flex;
            justify-content: center;
            align-items: center;

            .filter-year {
                background-color: #141414;
                border: none;
                color: white;
                font-size: 15px;
                font-weight: 600;
                letter-spacing: 1px;
            }


            .filter-type {
                margin-left: 50px;
                background-color: #141414;
                border: none;
                color: white;
                font-size: 15px;
                font-weight: 600;
                letter-spacing: 1px;
            }
        }
    }

    .container-cours-mobile {
        width: 100%;
        height: 80%;
        overflow: scroll;

        .line-cours-mobile {
            margin-top: 5%;
            width: 100%;
            height: auto;
            display: flex;
            border-bottom: 2px solid white;

            .for-contain {
                width: 100%;
                height: auto;
                display: flex;
                border-bottom: 2px solid white;
                justify-content: center;
                align-items: center;

                .container-mobile-card {
                    height: 400px;
                    width: 80%;
                    margin-bottom: 5%;
                    background-color: #fff;
                    border-radius: 10px;
                    overflow: hidden;
                    cursor: pointer;
                    position: relative;
                    color: white;
                    box-shadow: 0 10px 30px 5px rgba(0, 0, 0, 0.2);
                    display: flex;
                    justify-content: space-between;
                }
            }


        }
    }

}</style>